.content3{
    background-color: wheat;
    padding: 50px 50px;
}
.judul3{
    text-align: center;
    color: black;
}
.front{
    margin-top: 20px;
}
.content3 h3{
    padding-top: 70px;
    margin-left: 1.5%;
    font-size: 1.5em;
    color: black;
    font-weight: 700;
}
.skill-container {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;
}
.skill-card {
    flex: 0 1 22%;
    background-color: white;
    margin: 16px 1.5%;
    border-radius: 16px;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.3);
}
.skill-picture {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    height: auto;
    width: 50%;
    margin-bottom: 16px;
    border-radius: 16px;
}
.skill-title {
    margin-bottom: 16px;
    text-align: center;
   font-weight: 700;
    color: black;
}