* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
body{
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;
}

header{
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.logo{
    width: auto;
    cursor: pointer;
    margin: 12px 24px;
    padding: 8px 16px;
    color: black;
    font-size: 2em;
    font-weight: bold;
}

.nav-bar-items{
    list-style: none;
    display: flex;
}
.nav-bar li{
    padding: 16px 32px;
}
.nav-bar-items li a{
    font-weight: 700;
    text-decoration: none;
    color: black;
}