

.content6{
    background-color: whitesmoke;
    padding: 50px 50px;
}
.judul4{
    color: black;
    text-align: center;
}
.media-container{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;
}
.media-card{
    flex: 0 1 22%;
    background-color: navy;
    margin: 16px 1.5%;
    border-radius: 16px;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.3);
} 
.media-card:hover{
    box-shadow: 0px 0px 20px 0px #ffd700;
}
.media-icon{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    padding: 20px;
}
.media-judul{
    margin-bottom: 20px;
    text-align: center;
    font-weight: 200;
    color: white;
} 
a{
    text-decoration: none;
}