.content2{
    background-color: lightblue;
    padding: 50px 50px;
}
.judul2{
    text-align: center;
    padding-bottom: 10px;
    color: white;
}
.portfolio-card{
    width: 97%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 16px 1.5%;
    background-color: white;
    border-radius: 16px;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.3);
}
.portfolio-picture{ 
    height: 250px;
    width: 40%;
    border-radius: 16px 0px 0px 16px;
    object-fit: cover;
}
.portfolio-card div{
    width: 60%;
    margin-left: 16px;
    margin-right: 16px;
}
.title-2{
    padding-top: 96px;
    margin-left: 1.5%;
    font-size: 1.5em;
    color: #162783;
    font-weight: 700;
}
.title-3{
    color: #162783;
    margin: 16px 0px;
    font-size: 1.25em;
    font-weight: 700;
}
.description{
    line-height: 1.5;
}