footer{
    text-align: center;
    padding: 32px;
    background-color: #162783;
    color: white;
}

@media screen and (max-width: 700px) {
    header {
        position: static;
        flex-direction: column;
    }
    .nav-bar-items {
        flex-direction: column;
        text-align: center;
    }
    .profil-container {
        padding-top: 32px;
        flex-direction: column;
    }
    .portfolio-picture { 
        border-radius: 16px 16px 0px 0px;
    }
    .foto{
        display: block;
        margin-left: auto;
        margin: auto;
        width: 80%;
    }
    .bio {
        padding: 16px;
        border-radius: 0px 0px 20px 20px;
    }
    .portfolio-card {
        flex-direction: column;
        align-items: flex-start;
    }
    .portfolio-card div {
        width: 100%;
        margin: 16px;
    }
    .portfolio-picture {
        width: 100%;
        height: auto;
    }
    .portfolio-card .title-3{
        text-align: center;
    }
    .skill-card {
        flex: 0 1 47%;
    }
    .skill-picture{
        width: 50%;
    }
    .education-card {
        flex: 0 1 97%;
    }
    .media-card {
        flex: 0 1 47%;
    } 
}

@media screen and (max-width: 550px) {
    .judul1{
        font-size: 30px;
    }
    .nas{
        font-size: 14px;
    }
    .title-3{
        font-size: 18px;
    }
    .skill-card {
        flex: 0 1 47%;
    }
    .media-card {
        flex: 0 1 47%;
    }
    .media-judul{
        font-size: 12px;
        text-align: center;
    }
    .description{
        font-size: 10px;
    }
    .education-card {
        flex: 0 1 97%;
    }
}